'use strict'

import Conf from '../conf'
//import * as C from '../constants'
import isBoolean from 'lodash/isBoolean'

//const HOST = `${Conf.host}:${Conf.port}`
const HOST = Conf.host
const BASE_API_URL = `${HOST}/api/v1`
const ENDPOINTS = {
    login: `${BASE_API_URL}/login`,
    authCheck: `${BASE_API_URL}/auth_check`,
    posts: `${BASE_API_URL}/posts`,
    graphql: `${HOST}/graphql`
}

function _parseResponse(response, context) {

  console.log('response', response)

  // TODO: handle status codes
  return response.json().then(jsonObj => {
    return { json: jsonObj, status: response.status }
  })
}

function _handleError(error) {
  console.log('error', error)
}

export function graphql(query) {
  // single graphql endpoint
  const endpoint = `${ENDPOINTS.graphql}?query=${query}`
  return fetch(endpoint, {
    method: 'GET',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json'
    },
    credentials: 'include'
  })
  .then(response => _parseResponse(response))
  .catch(error => _handleError(error))
}

export function login(username, password) {
  const body = { username, password }
  return fetch(ENDPOINTS.login, {
    method: 'POST',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json'
    },
    body: JSON.stringify(body),
    credentials: 'include' // always need this for cookies to work
  })
  .then(response => _parseResponse(response))
  .catch(error => _handleError(error))
}

export function authCheck() {
  // checks if current user is authenticated
  // using flask session
  const body = {}
  return fetch(ENDPOINTS.authCheck, {
    method: 'GET',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json'
    },
    credentials: 'include'
  })
  .then(response => _parseResponse(response))
  .catch(error => _handleError(error))
}

export function createPost(post_form_data) {

  return fetch(ENDPOINTS.posts, {
    method: 'POST',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json'
    },
    body: JSON.stringify(post_form_data),
    credentials: 'include'
  })
  .then(response => _parseResponse(response))
  .catch(error => _handleError(error))
}

export function updatePost(post_id, post_form_data) {
  const endpoint = `${ENDPOINTS.posts}/${post_id}`
  return fetch(ENDPOINTS.posts, {
    method: 'POST',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json'
    },
    body: JSON.stringify(post_form_data),
    credentials: 'include'
  })
  .then(response => _parseResponse(response))
  .catch(error => _handleError(error))
}

export function getPosts(page, rpp) {
  page = page || 1
  rpp = rpp || 10
  const endpoint = `${ENDPOINTS.posts}?page=${page}&rpp=${rpp}`
  return fetch(endpoint, {
    method: 'GET',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json'
    },
    credentials: 'include'
  })
  .then(response => _parseResponse(response))
  .catch(error => _handleError(error))
}

export function deletePost(post_id) {
  const endpoint = `${ENDPOINTS.posts}/${post_id}`
  return fetch(endpoint, {
    method: 'DELETE',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json'
    },
    credentials: 'include'
  })
  .then(response => _parseResponse(response))
  .catch(error => _handleError(error))
}



