'use strict'
import includes from 'lodash/includes'
import values from 'lodash/values'
import * as types from '../actions/action-types'


// Throws error if condition is falsey.
export function assert(condition, message) {
  if (!condition) {
    if (typeof Error !== 'undefined') {
      throw new Error(message || 'Assertion failed')
    }
  }
}

export function createAction(type, payload=undefined, meta=undefined) {
  // Ensure action conforms to proper format
  assert(typeof type === 'string' && type.length > 0,
         `Type ${type} must be non-empty string!`)

  assert(includes(values(types), type),
        `Type ${type} must be a defined action type!`)

  assert(typeof payload === 'undefined' || (typeof payload === 'object' && payload !== null && Object.keys(payload).length),
         `${type} payload must be non-empty object or undefined!`)

  assert(typeof meta === 'undefined' || (typeof meta === 'object' && meta !== null && Object.keys(meta).length),
         `${type} meta must be non-empty object or undefined!`)

  return {
    type,
    ...(payload ? { payload } : {}),
    ...(meta ? { meta } : {})
  }
}

// Checks whether an action is an Effect (e.g. from Sagas)
export const isEffect = (action) => includes(action.type, 'EFFECT_')


export function findGetParameter(parameterName) {
    var result = null,
        tmp = [];
    location.search
        .substr(1)
        .split("&")
        .forEach(function (item) {
          tmp = item.split("=");
          if (tmp[0] === parameterName) result = decodeURIComponent(tmp[1]);
        });
    return result;
}


